.dialogs-list__item__container {
  padding-bottom: 25px;
  font-size: 16px;
  list-style-type: none;
  margin-bottom: -20px;
}

.dialogs-list__item__box-for-point {
  display: flex;
  align-items: center;
}

.dialogs-list__dot {
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background-color: rgb(92, 91, 91);
  margin-right: 5px;
}

.dialogs-list__item__inner {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: inherit;
  height: 50px;
  width: 50px;
}
.dialogs-list__item__inner:hover {
  width: 65px;
  height: 65px;
  font-weight: 700;
  font-size: 16px;
  transition: width 0.8s, height 0.8s, color 0.3s, transform 0.8s;
  color: gold;
}

._active {
  font-weight: 700;
  font-size: 16px;
  transition: width 0.8s, height 0.8s, color 0.3s, transform 0.8s;
  color: gold;
}

.dialogs-list__avatar {
  width: 100%;
  height: 100%;
  border-radius: 50px;
  margin: 0px 10px 0px 10px;
}

.dialogs-list__delete-dialog-btn {
  width: 120px;
  border-radius: 50px;
  background-color: #f9f5ff;
  color: #6941c6;
  border: none;
  box-shadow: none;
  margin: 3px 0px;
  font-size: 12px;
  font-weight: 500;
  display: flex;
  position: relative;
  bottom: 25px;
  left: 85px;
}

.dialogs-list__delete-dialog-btn__dot {
  background: #f04438;
  position: relative;
  border-radius: 50%;
  width: 4px;
  height: 4px;
  margin-right: 5px;
}

.dialogs-list__edit-toggler {
  position: relative;
  bottom: 25px;
  left: 95px;
  color: #101828;
}
